    @bind
    private async onClickSession(e: any, data: any) {
        const {
            clientStore
        } = this.props;

        if (_get(clientStore, 'deviceConfig.sessionToken')) {
            await clientStore.destroySession();
        }
        else {
            await clientStore.createSession();
        }
    }

    @bind
    private async onClickResetVAM(e: any, data: any) {
        const {
            clientStore
        } = this.props;

        await clientStore.resetDevice('VAM');
    }

    @bind
    private async onClickResetDevice(e: any, data: any) {
        const {
            clientStore
        } = this.props;

        await clientStore.resetDevice('DEVICE');
    }

    @bind
    private async OnClickClearInferenceImage(e: any, data: any) {
        const {
            clientStore
        } = this.props;

        clientStore.clearInference();

        this.setState({
            detected: false,
            detectedClasses: '',
            detectedImage: blankImage
        });
    }

    @bind
    private async onSelectNewModel(file: any) {
        const {
            clientStore
        } = this.props;

        const foo = 5;

        clientStore.uploadModel(file);
    }

